.cards{
     display: grid;
     grid-template-columns: repeat(2, 1fr);
     text-align: center;
     padding-top: 40px;
     padding-bottom: 40px;
     // grid-column-gap: 20px;
     grid-row-gap: 30px;
     position:sticky;
     .common, .rare, .epic, .legendary, .ultra{
          opacity: .6;
          font-size: 12px !important;
          // cursor: pointer;
     }
     .common{
          color: lightgreen;
     }
     .rare{
          color: cyan;
     }
     .epic{
          color: rgb(189, 59, 189);
     }
     .legendary{
          color: gold;
     }
     .ultra{
          color: grey;
     }
     img{
          cursor: pointer;
          transition-property: transform, padding;
          transition-duration: .5s;
          transition-timing-function: ease-in-out;
          transform: scale(1);
          padding: auto;
          &:hover{
               transform: scale(1.08);
               padding: 10px;
          }
     }
}